<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <!-- <span>成品出库</span> -->
        <CommonMissStationHeader></CommonMissStationHeader>
      </div>

      <el-row :gutter="20">
        <el-col :span="14">
          <el-form
            class=""
            style="padding: 0 60px 0 20px; width: 90%; margin: 20px 0 40px"
            ref="formRef"
            :model="form"
            :rules="formRules"
            label-width="150px"
          >
            <el-form-item label="发货数量" prop="pack_num_start">
              <div>
                <el-input
                  style="width: 50px"
                  v-model.number="form.pack_num_start"
                ></el-input>
                /
                <el-input
                  style="width: 50px"
                  v-model.number="form.pack_num_end"
                ></el-input>
              </div>
            </el-form-item>

            <el-form-item label="包装编码" prop="pack_id">
              <el-input
                ref="packInput"
                v-model="form.pack_id"
                @input="nextFocus()"
              ></el-input>
            </el-form-item>
          </el-form>

          <div>
            <el-button @click="resetForm">重 置</el-button>
            <el-button type="primary" @click="dialogSave">确 定</el-button>
          </div>
        </el-col>
        <el-col :span="10">
          <div ref="PieChart" class="chart chartClass"></div>
        </el-col>
      </el-row>
    </el-card>
    <!-- 帅选条件 -->
    <div class="formDiv mt20">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="时间">
          <el-date-picker
            v-model="listQuery.daterangeArr"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
            @change="daterange_change"
          >
          </el-date-picker>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查 询</el-button
          >
          <el-button size="medium" @click="resetTable()">重 置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <div slot="header" class="clearfix">
        <span>出库信息</span>
      </div>
      <div class="">
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="true"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
import CommonMissStationHeader from "@/components/CommonMissStationHeader";

import { formatDate } from "@/assets/libs/unit";
import ELTable from "@/components/formTablePage/ElTable";
import * as echarts from "echarts";

export default {
  name: "MissStation",
  computed: {
    ...mapState([
      "ProductionPlanCode",
      "WorkCodeNameList",
      "PersonCodeNameList",
    ]),
  },
  components: {
    CommonMissStationHeader,
    ELTable,
  },
  data() {
    return {
      myChart: null,
      seriesData: [],
      titleTotal: 0,
      timeout: "",
      Shipping_SN_length: "",
      Shipping_SN_length_flag: true,
      // 产品出库弹出层
      form: {
        pack_num_start: 1,
        pack_num_end: 20,
        pack_id: "",
        response_datas: [
          {
            pack_id: "",
          },
        ],
      },
      formBase: {
        pack_num_start: 1,
        pack_num_end: 20,
        pack_id: "",
        response_datas: [
          {
            pack_id: "",
          },
        ],
      },
      formRules: {
        pack_id: [
          { required: true, message: "请输入包装编码", trigger: "blur" },
        ],
      },
      listQuery: {
        daterangeArr: [
          formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
          formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        ],
        start_time: formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
        end_time: formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        daterangeArr: [
          formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
          formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        ],
        start_time: formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
        end_time: formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 表格列
      tableCol: [
        {
          label: "包装编码",
          field: "pack_id",
          align: "center",
        },

        {
          label: "料号",
          field: "flex_part_number",
          align: "center",
        },

        {
          label: "供应商编码",
          field: "vendor_code",
          align: "center",
        },
        {
          label: "包装数量",
          field: "part_qty",
          align: "center",
        },

        // {
        //   label: "工站名称",
        //   field: "work_name",
        //   align: "center",
        // },

        {
          label: "操作人员",
          field: "user_name",
          width: "200px",
          align: "center",
        },

        {
          label: "时间",
          field: "manufacturing_date",
          align: "center",
        },
      ],
      tableData: [],
    };
  },
  created() {
    window.addEventListener("keydown", this.handleKeyDown, true); //开启监听键盘按下事件
  },
  methods: {
    ...mapActions([
      "getLogisticProductPlanDeal",
      "getLogisticPersonCodeName",
      "getLogisticWorkCodeName",
    ]),
    nextFocus(nextRef, index, matter_code) {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        console.log(this.form.pack_id, this.form.pack_id.length);
        if (this.form.pack_id.length != this.Shipping_SN_length) {
          this.$message.warning(
            "包装编码长度为" + this.Shipping_SN_length + "个字节。"
          );
          this.Shipping_SN_length_flag = false;
          return false;
        }
        this.Shipping_SN_length_flag = true;

        this.dialogSave();
      
      }, 500);
    },

    // 打印参数列表 查询
    getLogisticOperationSystem() {
      baseManagement
        .getLogisticOperationSystem({
          page: 1,
          page_size: 10,
        })
        .then((res) => {
          console.log("打印参数列表 查询", res);
          if (res.code == 0) {
            this.Package_Qty = res.data.data[0].Package_Qty;
            this.Shipping_SN_length = res.data.data[0].Shipping_SN_length;
            this.Product_length = res.data.data[0].Product_length;

            console.log("this.Package_Qty ", this.Package_Qty);
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    // 产品出库 新增

    getLogisticstockInStockOut(params) {
      this.form.pack_num_start += 1;
      this.form.pack_id = "";
      this.$nextTick(() => {
        this.$refs.packInput.focus();
      });
   
    },

    // 重置按钮
    resetForm() {
      this.form.pack_id = "";
      this.$nextTick(() => {
        this.$refs.packInput.focus();
      });
    },

    // 发货 出货
    getLogisticFinishedProductOut(params) {
      baseManagement.getLogisticFinishedProductOut(params).then((res) => {
        console.log(" 发货 出货 新增", res);
        if (res.code == 0) {
          if (this.form.pack_num_start < this.form.pack_num_end) {
            this.form.pack_num_start += 1;
          }

          this.form.pack_id = "";
          this.getLogisticGetProductOutInfo(this.listQuery);
          var printWindow = window.open();
          printWindow.document.open("text/plain");
          printWindow.document.write(res.data);
          printWindow.document.close();
          printWindow.focus();
          printWindow.print();
          printWindow.close();

          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 确认按钮

    dialogSave() {
      console.log("this.form", this.form);
      if (!this.form.pack_num_start || !this.form.pack_num_end) {
        this.$message.warning("请输入发货数量!");
        return false;
      }

      if (
        typeof this.form.pack_num_start != "number" ||
        typeof this.form.pack_num_end != "number"
      ) {
        this.$message.warning("发货数量必须为数值!");
        return false;
      }
      if (this.form.pack_num_start > this.form.pack_num_end) {
        this.$message.warning("发货数量格式不正确!");
        return false;
      }

      if (!this.Shipping_SN_length_flag) {
        this.$message.warning(
          "包装编码长度为" + this.Shipping_SN_length + "个字节。"
        );
        return false;
      }
      this.$refs.formRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          let params = JSON.parse(JSON.stringify(this.form));
          params.package_sequence =
            params.pack_num_start + "-" + params.pack_num_end;
          console.log("产品出库", params);
          this.getLogisticFinishedProductOut(params);
        }
      });
    },

    // handleKeyDown(e) {
    //   let key = null;
    //   if (window.event === undefined) {
    //     key = e.keyCode;
    //   } else {
    //     key = window.event.keyCode;
    //   }
    //   if (key === 13) {
    //     this.dialogSave();
    //   }
    // },

    // 出库信息 查询

    daterange_change() {
      if (this.listQuery.daterangeArr.length > 0) {
        this.listQuery.start_time =
          this.listQuery.daterangeArr[0] + " 00:00:00";
        this.listQuery.end_time = this.listQuery.daterangeArr[1] + " 23:59:59";
      }
    },

    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticGetProductOutInfo(this.listQuery);
    },
    // 表格重置
    resetTable() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));

      this.getLogisticGetProductOutInfo(this.listQuery);
    },
    getLogisticGetProductOutInfo(params) {
      params.product_plan_code = this.ProductionPlanCode;
      baseManagement.getLogisticGetProductOutInfo(params).then((res) => {
        console.log("出库信息 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          this.seriesData = [{ value: res.data.product_num, name: "发货总数" }];
          this.titleTotal = res.data.product_num;
          this.chartInit();
          tableData.forEach((tableDataItem) => {
            this.PersonCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.operate_user == CodeNameListItem.user_code) {
                tableDataItem.user_name = CodeNameListItem.user_name;
              }
            });

            // this.WorkCodeNameList.forEach((CodeNameListItem) => {
            //   if (tableDataItem.work_code == CodeNameListItem.work_code) {
            //     tableDataItem.work_name = CodeNameListItem.work_name;
            //   }
            // });
          });

          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticGetProductOutInfo(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticGetProductOutInfo(this.listQuery);
    },

    chartInit() {
      let _this = this;
      if (
        this.myChart != null &&
        this.myChart != "" &&
        this.myChart != undefined
      ) {
        this.myChart.dispose();
      }
      this.myChart = echarts.init(this.$refs.PieChart);

      // this.myChart.showLoading();

      //   myChart.showLoading();
      let option = {};
      // this.noneFlag = true;
      this.noneFlag = false;
      for (let i in this.seriesData) {
        if (this.seriesData[i].value > 0) {
          this.noneFlag = true;
        }
      }

      if (this.noneFlag) {
        option = {
          title: {
            text: "总计:" + this.titleTotal,
            top: "5%",
            right: "8%",
            textStyle: {
              // color: "#fff",
              fontSize: "16",
            },
          },
          color: ["#5470c6", "#FAC858"],
          tooltip: {
            trigger: "item",
            formatter: (params) => {
              return (
                params.name +
                ":  " +
                params.value +
                " " +
                ((params.value / this.titleTotal) * 100).toFixed(2) +
                "%"
              );
            },
          },
          legend: {
            type: "scroll",
            orient: "vertical",
            top: "center",
            right: "3%",
            textStyle: {
              padding: 5,
            },

            // formatter: function (params) {
            //   console.log(12321321, params);
            //   let value = 0;
            //   for (let i in _this.seriesData) {
            //     if (params == _this.seriesData[i].name) {
            //       value = _this.seriesData[i].value;
            //     }
            //   }
            //   return `${params} - ${value}`;
            // },
          },
          series: [
            {
              // name: this.seriesName,
              // name: '公司所用工时',
              type: "pie",
              radius: ["45%", "75%"],
              center: ["50%", "50%"],
              avoidLabelOverlap: false,
              label: {
                show: false,
                position: "center",
              },
              // itemStyle: {
              //   normal: {
              //     borderColor: "#fff",
              //     borderWidth: 2,
              //   },
              // },
              emphasis: {
                label: {
                  show: true,
                  // fontSize: "20",
                  // fontWeight: "bold",
                },
              },
              label: {
                // show:noneFlag,
                formatter: " {b|{b}：}{c}  {per|{d}%}  ",
                // backgroundColor: '#F6F8FC',
                // borderColor: '#8C8D8E',
                // borderWidth: 1,
                borderRadius: 4,

                rich: {
                  // b: {
                  //     color: '#4C5058',
                  //     fontSize: 14,
                  //     fontWeight: 'bold',
                  //     lineHeight: 33
                  // },
                  per: {
                    // color: '#fff',
                    // backgroundColor: '#4C5058',
                    // padding: [3, 4],
                    borderRadius: 4,
                  },
                },
              },
              labelLine: {
                // show: false,
              },
              data: this.seriesData,
            },
          ],
        };
      } else {
        //  this.myChart.hideLoading();
        option = {
          title: {
            text: "无数据",
            top: "center",
            left: "center",
          },
          series: [],
        };
      }
      // this.myChart.hideLoading();
      this.myChart.setOption(option);
      this.myChart.on("click", function (params) {
        _this.$emit("pieChartClick", params);
      });

      window.addEventListener("resize", function () {
        _this.myChart.resize();
      });
    },
  },
  mounted() {
    // 打印参数列表 查询
    this.getLogisticOperationSystem();
    // 进行中的生产计划接口：
    this.getLogisticProductPlanDeal();

    // 人员内码和人员名称 查询
    this.getLogisticPersonCodeName();
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();

    this.getLogisticGetProductOutInfo(this.listQuery);
    this.$nextTick(() => {
      this.$refs.packInput.focus();
    });
  },
};
</script>

<style lang="scss" scoped>
.boxDiv span {
  font-size: 14px;
}
